import React, { useRef } from 'react'

const AddImage = props => {

    //const imgRef = useRef(null)

    /*
    const img_pathUrl = (input) => {
        console.log(input.target.files)
        imgRef.current.src = (window.URL ? URL : webkitURL).createObjectURL(input.target.files[0]);
    }
    */

    let content = (
        <div>
            <div>
                <input
                    accept="image/*,image/heif,image/heic,video/*,video/mp4"
                    multiple
                    type="file"
                    onChange={(input) => props.onChange(input)}
                />
                <div role="button" tabIndex="0">
                    <div>
                        <span>
                            <i role="img"></i>
                        </span>
                        <span dir="auto">
                            Photo/Video
                        </span>
                    </div>
                    <div data-visualcompletion="ignore">
                    </div>
                </div>
            </div>
        </div>
    )

    return content
}

export default AddImage